.footer {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background-color: $footer-bg;

  &__social {
    margin-left: 260px;
  }
}

.footer-container {
  width: 960px;
  margin: 0 auto;
  padding-top: 37px;
  padding-bottom: 38px;
}

.copyright {
  display: inline-block;

  font-size: 16px;
  font-family: "Aller-Light", "Arial", sans-serif;
  color: $font-dark;
}

.footer-nav {
  display: inline-flex;
  justify-content: space-around;
  width: 299px;

  &__link {
    margin-left: -3px;

    font-size: 16px;
    font-family: "Aller-Light", "Arial", sans-serif;
    color: $font-grey;

    &::before {
      content: "•";

      display: inline-block;
      margin: 0 3.5px;

      font-size: 8px;
      font-family: "Aller-Light", "Arial", sans-serif;
      vertical-align: middle;
      color: $font-grey;
    }
  }
}

.social {
  display: inline-flex;
  justify-content: space-evenly;
  width: 209px;

  &__link {
    margin-left: -3px;

    font-size: 16px;
    font-family: "Aller-Light", "Arial", sans-serif;
    color: $font-grey;
  }

  &__instagram {
    &::after {
      content: "•";

      display: inline-block;
      margin: 0 3.5px;

      font-size: 8px;
      font-family: "Aller-Light", "Arial", sans-serif;
      vertical-align: middle;
      color: $font-grey;
    }
  }

  &__twitter {
    &::after {
      content: "•";

      display: inline-block;
      margin: 0 5px;

      font-size: 8px;
      font-family: "Aller-Light", "Arial", sans-serif;
      vertical-align: middle;
      color: $font-grey;
    }
  }
}
