.switch {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background-color: $switch-bg;
}

.switch-container {
  display: flex;
  justify-content: center;
  width: 960px;
  margin: 0 auto;
  padding-top: 70px;
  padding-bottom: 69px;
}

.macro {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background: url("../img/macro-bg.jpg") no-repeat;
  background-position: center;
  background-position-y: -306px;

  &__header {
    font-weight: 300;
    font-size: 52px;
    font-family: "Aller-Bold", "Arial", sans-serif;
    color: $font-white;
    letter-spacing: -0.3px;
    text-shadow: 0 0 1px #000000;
  }

  &__subheader {
    width: 618px;
    margin-top: 11px;

    font-weight: 300;
    font-size: 37px;
    line-height: 40px;
    font-family: "Aller-Light", "Arial", sans-serif;
    color: $font-white;
    text-shadow: 0 0 1px #000000;
  }
}

.macro-container {
  width: 960px;
  margin: 0 auto;
  padding-top: 49px;
  padding-bottom: 297px;
}
