.contact {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background-color: $contact-bg;
  border-bottom: 1px solid $border;

  &__header {
    font-size: 36px;
    line-height: 27px;
    font-family: "Aller-Bold", "Arial", sans-serif;
    color: $font-dark;
    letter-spacing: -0.2px;
  }

  &__present {
    margin-top: 19px;

    font-weight: 400;
    font-size: 19px;
    line-height: 27px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-dark;
  }

  &__questions {
    margin-top: 27px;

    font-weight: 400;
    font-size: 19px;
    line-height: 27px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-dark;
  }

  &__col-second {
    margin-left: 38px;
  }
}

.contact-container {
  display: flex;
  width: 960px;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 76px;
}

.contact-person {
  display: flex;

  &__img {
    width: 94%;
    height: auto;

    border-radius: 100%;
  }

  &__name {
    font-weight: 700;
    font-size: 21px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-dark;
  }

  &__about {
    margin-top: 3px;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-dark;
  }
}

.person-img {
  display: inline-block;
}

.person-text {
  display: inline-block;
  width: 297px;
  margin-top: 4px;
  margin-left: 10px;
}

.person-second {
  margin-top: 25px;
}

.contact-col {
  display: inline-block;
}

.contact-col-first {
  width: 440px;
}

.contact-col-second {
  width: 477px;
}
