.intro {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background: url("../img/intro-bg.jpg") no-repeat top;

  &__header {
    font-size: 52px;
    font-family: "Aller-Bold", "Arial", sans-serif;
    color: $font-white;
    letter-spacing: -1.3px;
  }

  &__subheader {
    margin-top: 9px;

    font-weight: 400;
    font-size: 37px;
    line-height: 42px;
    font-family: "Aller-Light", "Arial", sans-serif;
    color: $font-white;
    text-shadow: 0 0 1px #000000;
  }

  &__text {
    width: 385px;
    margin-top: 7px;

    font-weight: 300;
    font-size: 21px;
    line-height: 26px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-white;
    text-shadow: 0 0 2px #000000;
  }

  &__iphone-back {
    position: absolute;
    top: 88px;
    left: 10px;
    z-index: 1;

    display: inline-block;
  }

  &__iphone-front {
    position: absolute;
    top: 7px;
    left: 104px;
    z-index: 2;

    display: inline-block;
  }

  &__platforms {
    margin-top: 10px;
  }
}

.intro-container {
  width: 960px;
  margin: 0 auto;
  padding-top: 50px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  width: 90px;
  height: 91px;

  background: url("../img/logo.png") no-repeat top;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  width: 328px;

  &__link {
    font-size: 18px;
    font-family: "Aller-Regular", "Arial", sans-serif;
    color: $font-white;
    text-shadow: 0 0 1px #555555;
  }
}

.banner {
  display: flex;
  justify-content: space-between;
  padding-top: 38px;

  &__btn-get {
    margin-top: 45px;
  }
}

.intro-col-first {
  display: inline-block;
  width: 490px;
}

.intro-col-second {
  position: relative;

  display: inline-block;
  width: 415px;
  height: 464px;
  overflow: hidden;
}

.platforms {
  display: flex;
  justify-content: space-between;
  width: 409px;
}
