.btn--green {
  background-color: $btn-green;
}

.btn--white {
  background-color: $btn-white;
}

.btn-get {
  display: inline-block;
  padding: 23px 128px;

  font-size: 18px;
  font-family: "Aller-Bold", "Arial", sans-serif;
  color: $font-white;
  letter-spacing: 0.3px;

  border-radius: 3px;

  transition: background-color 0.5s;

  &:hover {
    background-color: $btn-dark-green;

    transition: background-color 0.5s;
  }
}

.btn-switch {
  font-size: 16px;
  font-family: "Aller-Regular", "Arial", sans-serif;
  color: $font-grey;

  border: 1px solid $border;

  transition: background-color 0.3s;

  &__macro {
    padding: 16px 0;
    padding-right: 42px;
    padding-left: 35px;

    border-radius: 3px 0 0 3px;
  }

  &__filters {
    padding: 16px 0;
    padding-right: 39px;
    padding-left: 34px;

    border-right: 0;
    border-left: 0;
  }

  &__sharing {
    padding: 16px 0;
    padding-right: 33px;
    padding-left: 35px;

    border-radius: 0 3px 3px 0;
  }

  &:hover {
    color: $font-white;

    background-color: $btn-grey;

    transition: background-color 0.3s;
  }
}

.btn-appstore {
  width: 195px;
  height: 67px;

  background: url("../img/app-store.png") no-repeat top;
}

.btn-googleplay {
  width: 195px;
  height: 67px;

  background: url("../img/google-play.png") no-repeat top;
}
