.about {
  min-width: 1170px;
  max-width: 1600px;
  margin: 0 auto;

  background-color: $about-bg;
  border-bottom: 1px solid $border;

  &__header {
    font-size: 36px;
    font-family: "Aller-Bold", "Arial", sans-serif;
    color: $font-dark;
  }

  &__description {
    margin-top: 16px;

    font-weight: 400;
    font-size: 19px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-dark;
  }

  &__text {
    margin-top: 64px;
    margin-left: 51px;
  }

  &__slider {
    margin-top: 33px;
  }
}

.about-container {
  display: flex;
  width: 960px;
  margin: 0 auto;
  padding-top: 87px;
}

.about-img {
  height: 372px;
  overflow: hidden;
}

.about-text {
  width: 554px;
}

.slider {
  display: flex;
  justify-content: center;

  &__dot {
    display: inline-block;
    width: 12px;
    height: 12px;
    margin: 0 4px;

    background-color: $slider-dot;
    border-radius: 100%;

    &:hover {
      background-color: $slider-dot-hover;
    }
  }
}
